footer {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.footer-section {
	display: inline-block;
	max-width: 25%;
	text-align: left;
}

.footer-section-title {
	font-size: 16px;
	font-weight: 400;
}

.footer-section a {
	color: white;
	font-size: 12px;
	display: block;
	text-decoration: none;
	margin: 20px 0px;
}